/* 
========================================================================
    The card of images styling
========================================================================
 */


/* page styling */

body {
    margin: 0;
    padding: 0 0 100px 0;
    font-family: sans-serif;
    overflow-x: hidden;
    background-color: #69696b;
}


/* The application style */

.App {
    text-align: center;
    position: relative;
}


/* Application header */

header {
    color: white;
}


/* the navigation button style */

.next,
.previous {
    padding: 10px;
    background-color: linear-gradient(white 10%);
    border-radius: 15px;
}

.next:hover,
.previous:hover {
    background-color: #ffd54d;
}

.next {
    position: absolute;
    left: 20px;
}

.previous {
    position: absolute;
    right: 20px;
}


/* 
========================================================================
    The card/holder of images styling
========================================================================
 */


/* styling the image holder */

.card {
    opacity: 0.5;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.3);
    transition: all 300ms linear;
    background-color: #69696b;
    cursor: pointer;
    max-width: 800px;
    text-align: left;
    margin: 0 auto;
}


/* styling the image */

.card img {
    max-width: calc(100%);
    margin: 0 auto;
    display: block;
}


/* 
========================================================================
    The card/wrapper of images and its details styling
========================================================================
 */


/* images images */

.aboutImage {
    padding-left: 20px;
    padding-bottom: 10px;
}


/* Styling the image wrapper */

.cards-slider-wrapper {
    display: flex;
    position: absolute;
    transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
    justify-content: flex-start;
    overflow: hidden;
}


/* styling the image wrapper and holder */

.cards-slider-wrapper .card {
    flex: 1;
    min-width: 800px;
    opacity: 0.7;
    transform: scale(0.6);
    box-shadow: none;
    transition: opacity 300ms linear, border-color 300ms linear, background-color 300ms linear, transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}


/* 
========================================================================
    The card/holder of images styling
========================================================================
 */


/* styling the wrapper , holder and the description of the image */

.cards-slider-wrapper .card .aboutImage {
    opacity: 0;
    transition: opacity 150ms linear;
}


