/* 
========================================================================
    The pack of images styling
========================================================================
 */

.col {
    height: 610px;
    position: relative;
}


/* styling the image before and after being active */

.col:before,
.col:after {
    content: '';
    display: block;
    width: 50%;
    height: 105%;
    background: linear-gradient(to right, #69696b 10%, rgba(255, 255, 255, 0) 100%);
    position: absolute;
    top: 0;
    left: -200px;
    z-index: 2;
}

.col:after {
    left: auto;
    right: -200px;
    background: linear-gradient(to left, #69696b 10%, rgba(255, 255, 255, 0) 100%);
}


/* 
========================================================================
    The slideshow styling
========================================================================
 */

.cards-slider {
    position: relative;
    max-width: 800px;
    margin: 0 auto;
}

.cards-slider:after {
    content: '';
    display: block;
    width: 100%;
    height: 610px;
    /* outline: 5px solid #61DAFB; */
    position: absolute;
    top: 0;
    left: 400px;
}


/* 
========================================================================
    The footer styling
========================================================================
 */

.footer {
    padding: 10px;
    text-align: center;
    color: white;
}